/* App.css */

.app {
  max-width: 100%;
  margin: 0 auto;
  padding: 0;
}

.header {
  background-image: url('/public/laptop-1478822_1280.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
  padding: 100px 0; /* 필요에 따라 조절하세요 */
}

.header-title {
  font-size: 50px;
  font-weight: bolder;
  margin-bottom: 20px;
}

.header-content {
  margin: 20px;
  font-size: 20px;
}
/* styles.css */
.introduction {
  background-color: #f9f9f9;
  padding: 50px 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 30px;
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.about-item {
  flex: 1 1 200px;
  margin: 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.about-title {
  display: flex;
  flex-direction: row;
  align-items: end;
  justify-content: end;
}

.about-item i {
  font-size: 20px;
}

.about-item span {
  font-weight: bold;
  display: block;
  margin-top: 10px;
}

.about-item p {
  margin-top: 5px;
  font-size: 0.9em;
}

/* styles.css */
.skills {
  background-color: #ffe084;
  padding: 50px 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 30px;
}

.skills-content {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
}
.skills-title {
  width: 200px;
  margin-right: 20px;
}
.skill-category {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  margin: 20px;
  text-align: center;
}

.skill-category h3 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.skill-icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  justify-content: center;
  padding: 10px;
  border-radius: 10px;
  background-color: white;
}

.skill {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

.skill-icons img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin: 10px;
}
